function compileProgram(gl, source) {
	
  let prog = gl.createProgram();

  let vertexShader = loadShader(
    gl,
    gl.VERTEX_SHADER,
    `
	  #ifdef GL_ES
	  precision mediump float;
	  #endif
	  
	attribute vec3 aPos;
    attribute vec3 aNorm;
	attribute vec2 aTex;
	
	uniform vec3 testVec3;
	uniform mat4 projection;
	uniform mat4 view;
	uniform mat4 model;
	
	varying vec3 fragmentPosition;
	varying vec3 fragmentNormal;
	varying vec2 fragmentTexCoord;
	
	void main() { 
		
		fragmentPosition = aPos;
		fragmentNormal = aNorm;
		fragmentTexCoord = aTex;
	
	//gl_Position =  projection * view * model * vec4(aPos, 1.0);
	gl_Position = projection * view * model * vec4(aPos,1.0);//vec4(aPos.x*(sin(testVec3.x)),aPos.y+cos(testVec3.z),aPos.z, 1.0); 
	}`
  );
  let fragmentShader = loadShader(gl, gl.FRAGMENT_SHADER, source);


  gl.attachShader(prog, vertexShader);
  gl.attachShader(prog, fragmentShader);
  gl.linkProgram(prog);
  gl.deleteShader(vertexShader);
  gl.deleteShader(fragmentShader);

  if (!gl.getProgramParameter(prog, gl.LINK_STATUS)) {
    gl.deleteProgram(prog);
    throw new Error("kaputt");
  }

  gl.useProgram(prog);

  return prog;
}

function loadShader(gl, shaderType, src) {
  let shader = gl.createShader(shaderType);

  gl.shaderSource(shader, src);
  gl.compileShader(shader);

  if (!gl.getShaderParameter(shader, gl.COMPILE_STATUS)) {
    throw new Error(gl.getShaderInfoLog(shader));
  }

  return shader;
}



